// default values
(function() {
  'use strict';

  function logDefSecond(message, logLevel = 'debug') {
    console[logLevel](message);
  }
  function logDefFirst(logLevel = 'debug', message) {
    console[logLevel](message);
  }

  log('Defaults to debug');
  log('warn', 'But can also warn');
}());
